//PT Mono 400

@font-face {
  font-family: "PT-Mono";
  src: url("../fonts/pt-mono-400.woff2") format("woff2"), url("../fonts/pt-mono-400.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

